import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View,Panel } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: {},
			count:0,
			api:null,
		};
	}

	componentDidMount() {
			var sh = 0;
			var arr_2 = [];
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					connect.send("VKWebAppGetAuthToken", {"app_id": 7286662, "scope": "friends,status"});

					break;

				case 'VKWebAppAccessTokenReceived':
						this.setState({ api: e.detail.data.access_token});
						connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "1test", "params": {"user_id": this.state.fetchedUser.id, "v":"5.103", "access_token":"4eb7f5164eb7f5164eb7f516124ed8da9044eb74eb7f51610935c7cf8472a044d5ad4b3"}});

					break;

				case 'VKWebAppCallAPIMethodResult':
				  switch (e.detail.data.request_id) {
				  	case '1test':
						this.setState({count:e.detail.data.response.count});
						var pi = Math.floor((e.detail.data.response.count)/25);
						console.log(pi);
							for (let i = 0; i < pi; i++) {
								var arr = [];
								var ost = [];
								for(var t = 0;t < 25;t++){
									arr.push(e.detail.data.response.items[i*25+t]);

								}
								t = 0;
								connect.send("VKWebAppCallAPIMethod", {"method": "execute.test_t","request_id": "7test", "params": {"array": arr, "v": "5.103","access_token": this.state.api}});
							}
							 for(let i = 0; i < (e.detail.data.response.count-25*pi);i++){
								 	ost.push(e.detail.data.response.items[pi*25+i]);
							 }
							 connect.send("VKWebAppCallAPIMethod", {"method": "execute.test_t","request_id": "7test","params": {"array": ost, "v": "5.103","access_token": this.state.api}});
				  		break;
						case '2test':
            this.setState({count:e.detail.data.response.count+this.state.count});
						for (var i = 0; i < e.detail.data.response.items.length; i++) {
							//connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "3test", "params": {"user_id": e.detail.data.response.items[i], "v":"5.103", "access_token":"ad954c8fad954c8fad954c8fd0adfa6309aad95ad954c8ff3b6151cf42da6220c2f88d8"}});
						}
							break;
						case '3test':
              this.setState({count:e.detail.data.response.count+this.state.count});
							break;
						case '7test':

						for(let i = 0;i < e.detail.data.response.length;i++){

							for(let r = 0;r < e.detail.data.response[i].length;r++){
								  arr_2.push(e.detail.data.response[i][r])
							}
						}
						console.log(arr_2);

							break;
				  	default:
              //e.detail.data.response
              console.log(e.detail.data);
				  }

					break;
				default:
					console.log(e.detail);


			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
			<Panel id="home">
			  {this.state.count}
			</Panel>
			</View>
		);
	}
}

export default App;
